<template>
    <div class="search">
        <!-- 头部 -->
        <Header :type="'movie'" />
        <!-- main -->
        <main class="search-main">
            <section class="search-input">
                <!-- 搜索框 -->
                <div class="search-box">
                    <img src="@/assets/serch-bs.png" height="15" />
                    <input
                        type="text"
                        placeholder="搜电影、搜影院"
                        v-model="content"
                        @keyup="OPEN_Search()"
                    />
                    <router-link to="/movie">取消</router-link>
                </div>
            </section>
            <section class="search-row">
                <!-- 电影 -->
                <div class="search-movie">
                    <h3>电影/电视剧/综艺</h3>
                    <ul class="Movie-row-ul">
                        <!-- row -->
                        <li class="Movie-row-img" v-for="i in SearchContent.movie" :key="i.id">
                            <!-- 电影图片 -->
                            <div class="Movie-row-img">
                                <img :src="i.img | WH('128.180')" height="100" />
                            </div>
                            <!-- 电影内容 -->
                            <div class="Movie-row-content">
                                <h3>{{ i.nm }}</h3>
                                <p>
                                    观众点评
                                    <span class="review">{{ i.sc }}</span>
                                    <br />
                                    <span class="starring">主演 {{ i.star }}</span>
                                    <!-- 播放 -->
                                    {{ i.showInfo }}
                                </p>
                            </div>
                            <!-- 交互按钮 -->
                            <div class="Movie-row-interactive">
                                <button>购票</button>
                            </div>
                        </li>
                        <!-- row - end -->
                    </ul>
                </div>
                <!-- 影院 -->
                <div class="search-cinema">
                    <h3>影院</h3>
                    <!-- row -->
                    <div class="cinema-row" v-for="i in SearchContent.cinema" :key="i.id">
                        <!-- title -->
                        <h3>
                            {{i.nm}}
                            <span>{{i.sellPrice}}元起</span>
                        </h3>
                        <!-- 位置 -->
                        <section class="cinema-row-location box-flex">
                            <div class="location-arr">{{i.addr}}</div>
                            <div>{{i.distance}}</div>
                        </section>
                        <!-- 标签 -->
                        <section class="cinema-row-lable box-flex">
                            <div>改签</div>
                            <div>小吃</div>
                            <div>折扣卡</div>
                            <div>杜比全景声厅</div>
                        </section>
                    </div>
                </div>
            </section>
        </main>
        <!-- 导航 -->
        <Footer />
    </div>
</template>



<script>
import Header from "./Movie-header";
import Footer from "./Movie-footer";
import { setTimeout } from "timers";

export default {
    name: "search",
    components: {
        Header,
        Footer
    },
    data() {
        return {
            content: "", // 内容
            SearchContent: {
                movie: {},
                cinema: {}
            }
        };
    },
    methods: {
        // OPEN
        OPEN_Search() {
            if (this.content === "") {
                this.SearchContent.movie = {};
                this.SearchContent.cinema = {};
                return;
            }
            clearTimeout(timer);
            let timer = setTimeout(() => {
                this.axios
                    .get(`http://39.97.33.178/api/searchList?cityId=30&kw=${this.content}`)
                    .then(res => {
                        try {
                            this.SearchContent.movie =
                                res.data.data.movies.list;
                            this.SearchContent.cinema =
                                res.data.data.cinemas.list;
                        } catch (err) {
                            console.log(err);
                        }
                        console.log(res.data);
                    })
                    .catch(err => {
                        console.log(err);
                    });
            }, 500);
        }
    }
};
</script>


<style scoped>
.search-main {
    height: 80vh;
    background-color: #f5f5f5;
}
.search-input {
    height: auto;
    border-bottom: 1px solid #e5e5e5;
    padding: 0.5rem;
    box-sizing: border-box;
}

/* 搜索框 */
.search-box {
    text-align: left;
    position: relative;
}
.search-box > img {
    position: absolute;
    transform: translate(5px, 5px);
}
.search-box > input {
    width: calc(100% - 40px);
    padding: 0.3rem 0.3rem 0.3rem 1.5rem;
    border-radius: 5px;
    border: 1px solid #e5e5e5;
    box-sizing: border-box;
}
.search-box > a {
    color: #f03d37;
    padding-left: 0.5rem;
}

.search-movie,
.search-cinema {
    background-color: #fff;
    margin-bottom: 15px;
}

.search-movie > h3,
.search-cinema > h3 {
    border-bottom: 1px solid #e5e5e5;
    text-align: left;
    font-size: 0.9rem;
    padding: 0.8rem;
    color: #999;
}
.Movie-row-ul > li {
    display: flex;
    padding: 20px 10px;
    width: 100%;
    box-sizing: border-box;
}
.cinema-row {
    padding: 1rem 0;
}
.Movie-row-img > img {
    width: 100%;
    height: 100%;
}
@import "../../public/css/movie.css";
@import "../../public/css/cinema.css";
</style>

